// @mui material components
import Grid from "@mui/material/Grid";

// Material Dashboard 2 React components
import MDBox from "components/MDBox";

// Material Dashboard 2 React example components
import DashboardLayout from "examples/LayoutContainers/DashboardLayout";
import DashboardNavbar from "examples/Navbars/DashboardNavbar";
import Footer from "examples/Footer";
// import ReportsBarChart from "examples/Charts/BarCharts/ReportsBarChart";
// import ReportsLineChart from "examples/Charts/LineCharts/ReportsLineChart";
import ComplexStatisticsCard from "examples/Cards/StatisticsCards/ComplexStatisticsCard";

// Data
// import reportsBarChartData from "layouts/masolution/data/reportsBarChartData";
// import reportsLineChartData from "layouts/masolution/data/reportsLineChartData";

// Dashboard components
// import Projects from "layouts/masolution/components/Projects";
// import OrdersOverview from "layouts/masolution/components/OrdersOverview";
import React, { useState } from "react";

import DefaultProjectCard from "examples/Cards/ProjectCards/DefaultProjectCard";

import MDSnackbar from "components/MDSnackbar";
import MDButton from "components/MDButton";
import port1 from "assets/images/port_1.png";
import port2 from "assets/images/port_2.png";
import port3 from "assets/images/port_3.png";
import port4 from "assets/images/port_4.png";
import port5 from "assets/images/port_5.png";

function Dashboard() {
  const [warningSB, setWarningSB] = useState(false);
  const [port, setPort] = useState(" ");
  sessionStorage.setItem("selected1", false);
  const closeWarningSB = () => setWarningSB(false);
  window.addEventListener("port1", () => {
    console.log("change to local storage!");
    setPort(localStorage.getItem("port"));
    console.log(localStorage.getItem("port"));
    let portnm = "1";
    if (sessionStorage.getItem("port1") === "") {
      setPort(port1);
      portnm = "1";
    } else if (sessionStorage.getItem("port1") === "") {
      setPort(port2);
      portnm = "1";
    } else if (sessionStorage.getItem("port1") === "") {
      setPort(port3);
      portnm = "1";
    } else if (sessionStorage.getItem("port1") === "") {
      setPort(port4);
      portnm = "1";
    } else if (sessionStorage.getItem("port1") === "AI  ") {
      setPort(port5);
      portnm = "AI";
    }
    localStorage.setItem("port", portnm);
  });

  const renderWarningSB = (
    <MDSnackbar
      color="warning"
      icon="star"
      title="Material Dashboard"
      content="Hello, world! This is a notification message"
      dateTime="11 mins ago"
      open={warningSB}
      onClose={closeWarningSB}
      close={closeWarningSB}
      bgWhite
    />
  );
  // todo:
  // openWarningSB
  function openWarningSB() {
    console.log("on click!");
    if (sessionStorage.getItem("selected1") === "true") {
      console.log("selected");
      window.location.href = "{{ 'layouts/masolution1' | relative_url }}";
    }
  }
  return (
    <DashboardLayout>
      <DashboardNavbar />
      <MDBox py={3}>
        <Grid container spacing={3}>
          <Grid item xs={9.6} md={4.8} lg={3}>
            <MDBox mb={1.5}>
              <ComplexStatisticsCard
                color="dark"
                icon="weekend"
                count=" "
                title=" / ETF"
                solutionNum="1"
                percentage={{
                  color: "success",
                  amount: "+25%",
                  label: " ",
                }}
              />
            </MDBox>
          </Grid>
          <Grid item xs={9.6} md={4.8} lg={3}>
            <MDBox mb={1.5}>
              <ComplexStatisticsCard
                icon="leaderboard"
                count=" "
                title=" / ETF"
                solutionNum="1"
                percentage={{
                  color: "success",
                  amount: "+31%",
                  label: " ",
                }}
              />
            </MDBox>
          </Grid>
          <Grid item xs={9.6} md={4.8} lg={3}>
            <MDBox mb={1.5}>
              <ComplexStatisticsCard
                color="success"
                icon="store"
                count=" "
                title=" / ETF"
                solutionNum="1"
                percentage={{
                  color: "success",
                  amount: "+13%",
                  label: " ",
                }}
              />
            </MDBox>
          </Grid>
          <Grid item xs={9.6} md={4.8} lg={3}>
            <MDBox mb={1.5}>
              <ComplexStatisticsCard
                color="primary"
                icon="person_add"
                count=" "
                title="  "
                solutionNum="1"
                percentage={{
                  color: "success",
                  amount: "+16%",
                  label: " ",
                }}
              />
            </MDBox>
          </Grid>
        </Grid>
        <MDBox mt={4.5}>
          <Grid container spacing={3}>
            <Grid item xs={15} md={15} xl={12}>
              <DefaultProjectCard image={port} label="" title="" description="" />
            </Grid>
          </Grid>
        </MDBox>

        <MDBox>
          <MDBox mt={4}>
            <Grid container spacing={3}>
              <Grid item xs={12} sm={12} lg={12}>
                <MDButton
                  variant="gradient"
                  color="warning"
                  onClick={() => openWarningSB()}
                  fullWidth
                >
                  NEXT
                </MDButton>
                {renderWarningSB}
              </Grid>
            </Grid>
          </MDBox>
        </MDBox>
      </MDBox>
      <Footer />
    </DashboardLayout>
  );
}

export default Dashboard;
